___CSS_LOADER_EXPORT___.push([module.id, `.posts {
    display: grid;
    grid-row-gap: 16px;
}

@media screen and (max-width: 425px){
    .navbar-brand{
        font-size: 45px;
    }

    .navbar-brand img{
        display: none;
    }

    .post-item__title{
        margin-top: 0;
    }

    .post-item__title a {
        font-size: 30px;
    }
}

@media screen and (min-width: 864px) {
    .posts {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-column-gap: 10px;
        grid-row-gap: 16px;
    }

}

@media screen and (max-width: 855px) {
    .hero__content {
        margin: 0 auto;
        max-width: 500px;
    }

    .hero__title {
        font-size: 40px;
    }

    .explore__label {
        font-size: 40px;
    }

    .hero__description {
        font-size: 20px;
    }

    .hero__tagline {
        font-size: 40px;
    }

    .post-item__title {
        font-size: 35px;
    }

    .post-item__date {
        font-size: 15px;
    }

    .wrapper {
        grid-template-columns: repeat(2, 1fr);
    }

    .box1,
    .box2,
    .box3,
    .box4,
    .box7 {
        grid-column-start: 1;
        grid-column-end: 3;
    }

    .box1 h1 {
        font-size: 40px;
    }

    .wrapper h4 {
        font-size: 20px;
    }

    .wrappper th {
        width: 10%;
    }

    tr {
        font-size: 15px;
    }

    .custName {
        font-size: 17px;
    }

    .custDate {
        font-size: 17px;
    }

    .menu-item {
        font-size: 15px;
    }

    .reviewText {
        font-size: 15px;
    }
}

@media screen and (min-width: 1050px) {
    .post-item__content {
        padding: 16px 32px 32px 32px;
    }
}


@media screen and (min-width: 1200px) {
    .hero__img {
        min-width: 1000px;
    }

    .hero__img img {
        min-width: 1000px;
    }
}

@media screen and (min-width: 1300px) {

    .header__menu {
        display: none;
    }

    .nav-item {
        display: flex;
    }   
}


@media screen and (max-width: 1301px) {
    .nav {
        z-index: 10;
        background-color: #fff;
        width: 100%;
        position: absolute;

        /* This trasform moves the drawer off canvas. */
        -webkit-transform: translate(-2000px, 0);
        transform: translate(-2000px, 0);

        /* Optionally, we animate the drawer. */
        transition: transform 0.3s ease;
        display: block;

    }

    .open {
        -webkit-transform: translate(0, 0);
        transform: translate(0, 0);
    }

    .nav__item {
        display: list-item;
        border-bottom: 1px solid #E0E0E0;
        width: 100%;
        text-align: left;
        list-style: none;
        text-align: center;
        font-family: 'Graduate', cursive;
    }
}

@media screen and (max-width: 1212px) {
    th {
        width: 30%;
    }
}

@media screen and (min-width: 1380px) {

    .posts {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
    }
}

@media screen and (min-width: 1500px) {
    .headline {
        display: grid;
        grid-template-columns: 1fr 1fr;
    }

}

@media screen and (min-width: 1600px) {
    main {
        max-width: 1600px;
    }
}`, "",{"version":3,"sources":["webpack://./src/styles/responsive.css"],"names":[],"mappings":"AAEA;IACI,aAAa;IACb,kBAAkB;AACtB;;AAEA;IACI;QACI,eAAe;IACnB;;IAEA;QACI,aAAa;IACjB;;IAEA;QACI,aAAa;IACjB;;IAEA;QACI,eAAe;IACnB;AACJ;;AAEA;IACI;QACI,aAAa;QACb,8BAA8B;QAC9B,qBAAqB;QACrB,kBAAkB;IACtB;;AAEJ;;AAEA;IACI;QACI,cAAc;QACd,gBAAgB;IACpB;;IAEA;QACI,eAAe;IACnB;;IAEA;QACI,eAAe;IACnB;;IAEA;QACI,eAAe;IACnB;;IAEA;QACI,eAAe;IACnB;;IAEA;QACI,eAAe;IACnB;;IAEA;QACI,eAAe;IACnB;;IAEA;QACI,qCAAqC;IACzC;;IAEA;;;;;QAKI,oBAAoB;QACpB,kBAAkB;IACtB;;IAEA;QACI,eAAe;IACnB;;IAEA;QACI,eAAe;IACnB;;IAEA;QACI,UAAU;IACd;;IAEA;QACI,eAAe;IACnB;;IAEA;QACI,eAAe;IACnB;;IAEA;QACI,eAAe;IACnB;;IAEA;QACI,eAAe;IACnB;;IAEA;QACI,eAAe;IACnB;AACJ;;AAEA;IACI;QACI,4BAA4B;IAChC;AACJ;;;AAGA;IACI;QACI,iBAAiB;IACrB;;IAEA;QACI,iBAAiB;IACrB;AACJ;;AAEA;;IAEI;QACI,aAAa;IACjB;;IAEA;QACI,aAAa;IACjB;AACJ;;;AAGA;IACI;QACI,WAAW;QACX,sBAAsB;QACtB,WAAW;QACX,kBAAkB;;QAElB,+CAA+C;QAC/C,wCAAwC;QACxC,gCAAgC;;QAEhC,uCAAuC;QACvC,+BAA+B;QAC/B,cAAc;;IAElB;;IAEA;QACI,kCAAkC;QAClC,0BAA0B;IAC9B;;IAEA;QACI,kBAAkB;QAClB,gCAAgC;QAChC,WAAW;QACX,gBAAgB;QAChB,gBAAgB;QAChB,kBAAkB;QAClB,gCAAgC;IACpC;AACJ;;AAEA;IACI;QACI,UAAU;IACd;AACJ;;AAEA;;IAEI;QACI,aAAa;QACb,kCAAkC;IACtC;AACJ;;AAEA;IACI;QACI,aAAa;QACb,8BAA8B;IAClC;;AAEJ;;AAEA;IACI;QACI,iBAAiB;IACrB;AACJ","sourcesContent":["@import url('https://fonts.googleapis.com/css2?family=Graduate&display=swap');\n\n.posts {\n    display: grid;\n    grid-row-gap: 16px;\n}\n\n@media screen and (max-width: 425px){\n    .navbar-brand{\n        font-size: 45px;\n    }\n\n    .navbar-brand img{\n        display: none;\n    }\n\n    .post-item__title{\n        margin-top: 0;\n    }\n\n    .post-item__title a {\n        font-size: 30px;\n    }\n}\n\n@media screen and (min-width: 864px) {\n    .posts {\n        display: grid;\n        grid-template-columns: 1fr 1fr;\n        grid-column-gap: 10px;\n        grid-row-gap: 16px;\n    }\n\n}\n\n@media screen and (max-width: 855px) {\n    .hero__content {\n        ma