  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(241, 240, 240);
  color: rgb(0, 0, 0);
}

main {
  width: 100%;
  margin: 0 auto;
}

.navbar {
  background-color: #363636;
  height: 120px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 40px 40px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
}

