.hero {
  width: 100%;
  height: 43rem;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.hero__img {
  width: 100%;
  height: 100%;
  display: flex;
}

.hero__img img {
  object-fit: cover;
  width: 100%;
  height: 43rem;
}

.hero__content {
  width: 85%;
  text-align: center;
  background-color: rgba(0, 0, 0, 0);
  padding: 2rem;
  border-radius: 2rem;
  color: var(--green);
  left: 0;
  right: 0;
  margin: auto;
  position: absolute;
}

.hero__title {
  color: #fff;
  font-weight: 500;
  font-size: 65px;
  font-family: 'Graduate', cursive;
}

.hero__title span:first-child {
  color: var(--green);
}

.hero__title span:last-child {
  color: var(--orange);
}

.hero__description {
  color: #fff;
  margin-top: 16px;
  font-size: 30px;
  font-weight: 300;
  font-family: 'Graduate', cursive;
}

.navbar-brand {
  display: flex;
  align-items: center;
  color: #ffffff;
  font-size: 44px;
  font-family: 'Graduate', cursive;
  text-decoration: none;
}

.navbar-brand img {
  height: 90px;
  margin-right :10px;
  border-radius: 100%;
}

.navbar-nav {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

.nav-item {
  margin: 0 20px;
  display: none;
}

.nav-link {
  color: #ffffff;
  font-size: 44px;
  text-decoration: none;
  font-family: 'Graduate', cursive;
}

.nav-link:hover {
  color: gray;
}

.header__menu {
  font-size: 44px;
  margin: 10px auto;
  width: 44px;
  display: flex;
  color: #ffffff;
  background-color: #36363600;
  border: none;
}

.content {
  padding: 16px;
  /* border: 1px solid white; */
}

.explore {
  width: 100%;
  margin: 30px auto;
  text-align: center;
  /* border: 1px solid white; */
  padding: 5px;
  background-color: rgba(0, 0, 0, 0);
  padding: 30px 30px;
  border-radius: 10px;
}

.explore__label {
  font-size: 50px;
  font-family: 'Oswald', sans-serif;
}

.lineExplore {
  background-color: gray;
  height: 7px;
  width: 100px;
  margin: 0 auto;
  margin-top: 10px;
}

.favorite__label {
  font-size: 50px;
  font-family: 'Oswald', sans-serif;
  text-align: left;
}

.lineFavorite {
  background-color: gray;
  height: 7px;
  width: 100px;
  margin-top: 10px;
}

/*
 * post
 */

.posts {
  margin: 32px auto auto;
  text-align: left;
}

/*
 * post item
 */

.post-item {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  width: 100%;
  border-radius: 5px;
  overflow: hidden;
}

.post-item__content {
  padding: 16px;
}

.post-item__thumbnail {
  width: 100%;
  height: 70%;
}

.post-item__date {
  font-size: 20px;
  text-transform: uppercase;
  color: #999;
}

.post-item__date__author {
  color: red;
  text-decoration: none;
}

.post-item__title {
  font-weight: 500;
  font-size: 44px;
  transition: 0.3s opacity;
}

.post-item__title:hover {
  color: gray;
}

.post-item__title a {
  text-decoration: none;
  color: inherit;
}

.post-item__description {
  margin-top: 16px;
  font-size: 25px;
  line-height: 1.5em;
  text-align: justify;
}

.nav {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  display: none;
}

.nav__list {
  width: 100%;
  padding: 0;
  margin: 0;
}

.nav__item {
  box-sizing: border-box;
  display: inline-block;
  width: 24%;
  font-size: 44px;
  text-align: center;
  line-height: 24px;
  text-transform: uppercase;
}

.nav a {
  display: inline-block;
  padding: 1.3em;
  text-decoration: none;
  color: #616161;
}

.nav a:hover {
  text-decoration: underline;
  color: gray;
}

.skip-to-content {
  position: absolute;
  top: -40px;
  left: 0;
  background-color: #bf1722;
  color: white;
  padding: 8px;
  z-index: 100;
  text-decoration: none;
  font-family: sans-serif;
}

.skip-to-content:focus {
  top: 0;
}

footer {
  background-color: #363636;
  text-align: center;
  padding: 40px;
  margin-top: 50px;
}

footer p {
  font-size: 30px;
  color: #ffffff;
  margin: 0;
  font-family: 'Graduate', cursive;
}

/*Detail.css*/
.container-detail {
  padding: 20px;
  font-family: 'Roboto', sans-serif;
}

.wrapper {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  padding: 20px;
  column-gap: 10px;
  row-gap: 10px;
  /* border: 1px solid black; */
  background-color: rgba(0, 0, 0, 0);
  border-radius: 20px;
}

.wrapper div {
  /* border: 1px solid black; */
  padding: 16px;
}

.wrapper td {
  width: 100%;
  border-bottom: 1px solid #000746;
}

.wrapper th {
  text-align: left;
  width: 20%;
  border-bottom: 2px solid #000000;
}

.wrapper table {
  width: 100%;

}

.wrapper tr {
  height: 50px;
  font-size: 18px;
}

.list-menu,
.menu-item {
  text-align: left;
  list-style: none;
  font-size: 20px;
  line-height: 3;
}

.list-review,
.item-list-review {
  list-style: none;
  font-size: 20px;
  line-height: 2;
}


.box1 {
  color: black;
  grid-column-start: 1;
  grid-column-end: 4;
  text-align: left;
  padding: 0;
  width: 100%;
  height: 70%;
}

.box2 {
  grid-column-start: 1;
  grid-column-end: 3;
  padding: 0;
  /* border: 1px solid white; */
}

.box3 {
  grid-column-start: 3;
  grid-column-end: 4;
  padding: 0;
  width: 100%;
  /* margin: 0 auto; */
  /* width: 80%; */
  /* border: 2px solid yellow; */
}

.box4 {
  padding: 0;
  grid-column-start: 1;
  grid-column-end: 3;
  width: auto;
  text-align: justify;
}

.box5 {
  grid-column-start: 1;
  grid-column-end: 2;
  border-radius: 10px;
}

.box6 {
  grid-column-start: 2;
  grid-column-end: 3;
  border-radius: 10px;
}

.box7 {
  grid-column-start: 1;
  grid-column-end: 3;
}

.info-box {
  /* border: 1px solid rgb(111, 111, 111); */
  background-color: rgba(255, 255, 255, 0);
  border-radius: 10px;
  margin: 0 auto;
  height: 30%;
}

#desc-box {
  /* border: 1px solid rgb(111, 111, 111); */
  background-color: rgba(111, 111, 111, 0);
  border-radius: 10px;
  line-height: 30px;
  padding: 0;
}

#foods-box {
  /* border: 1px solid rgb(111, 111, 111); */
  background-color: rgb(111, 111, 111, 0);
  border-radius: 10px;
  padding: 0;
}

#drinks-box {
  /* border: 1px solid rgb(111, 111, 111); */
  background-color: rgb(111, 111, 111, 0);
  border-radius: 10px;
  padding: 0;
}

#customerReview-box {
  /* border: 1px solid rgb(111, 111, 111); */
  background-color: rgb(111, 111, 111, 0);
  border-radius: 10px;
  padding: 0;
}

#review-container {
  padding: 0;
}

img#restaurant-img {
  margin: 0 auto;
  max-width: 100%;
  height: auto;
  object-fit: cover;
  /* border: 1px solid rgb(111, 111, 111); */
  border-radius: 10px;
}

.wrapper h4 {
  text-align: left;
  font-size: 30px;
  color: rgb(0, 0, 0);
  font-family: 'Oswald', sans-serif;
}

.box1 h1 {
  font-size: 50px;
  font-family: 'Oswald', sans-serif;
}

.custName {
  font-weight: bold;
  color: rgb(70, 70, 70);
}

.custDate {
  color: rgb(138, 138, 138);
}

.reviewText {
  border: 1px solid rgb(222, 221, 221);
  padding: 5px;
  margin-bottom: 15px;
  border-radius: 7px;
  color: rgb(104, 104, 104);
  font-family: 'Roboto', sans-serif;
}

.wrapper hr {
  background-color: black;
  width: 100px;
  margin-top: 10px;
  margin-bottom: 10px;
  height: 6px;
  border: none;
}

.list-menu hr {
  background-color: rgb(104, 104, 104);
  width: 100%;
  height: 1px;
}

/*
  Like
*/
.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: gray;

  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;

  font-size: 18px;
  color: white;
  cursor: pointer;
}

#not-found{
  font-family: 'Roboto', sans-serif;
  color: gray;
  font-size: 20px;
  text-align: left;
  text-decoration: underline;
