ight: 43rem;\n}\n\n.hero__content {\n  width: 85%;\n  text-align: center;\n  background-color: rgba(0, 0, 0, 0);\n  padding: 2rem;\n  border-radius: 2rem;\n  color: var(--green);\n  left: 0;\n  right: 0;\n  margin: auto;\n  position: absolute;\n}\n\n.hero__title {\n  color: #fff;\n  font-weight: 500;\n  font-size: 65px;\n  font-family: 'Graduate', cursive;\n}\n\n.hero__title span:first-child {\n  color: var(--green);\n}\n\n.hero__title span:last-child {\n  color: var(--orange);\n}\n\n.hero__description {\n  color: #fff;\n  margin-top: 16px;\n  font-size: 30px;\n  font-weight: 300;\n  font-family: 'Graduate', cursive;\n}\n\n.navbar-brand {\n  display: flex;\n  align-items: center;\n  color: #ffffff;\n  font-size: 44px;\n  font-family: 'Graduate', cursive;\n  text-decoration: none;\n}\n\n.navbar-brand img {\n  height: 90px;\n  margin-right :10px;\n  border-radius: 100%;\n}\n\n.navbar-nav {\n  display: flex;\n  list-style: none;\n  margin: 0;\n  padding: 0;\n}\n\n.nav-item {\n  margin: 0 20px;\n  display: none;\n}\n\n.nav-link {\n  color: #ffffff;\n  font-size: 44px;\n  text-decoration: none;\n  font-family: 'Graduate', cursive;\n}\n\n.nav-link:hover {\n  color: gray;\n}\n\n.header__menu {\n  font-size: 44px;\n  margin: 10px auto;\n  width: 44px;\n  display: flex;\n  color: #ffffff;\n  background-color: #36363600;\n  border: none;\n}\n\n.content {\n  padding: 16px;\n  /* border: 1px solid white; */\n}\n\n.explore {\n  width: 100%;\n  margin: 30px auto;\n  text-align: center;\n  /* border: 1px solid white; */\n  padding: 5px;\n  background-color: rgba(0, 0, 0, 0);\n  padding: 30px 30px;\n  border-radius: 10px;\n}\n\n.explore__label {\n  font-size: 50px;\n  font-family: 'Oswald', sans-serif;\n}\n\n.lineExplore {\n  background-color: gray;\n  height: 7px;\n  width: 100px;\n  margin: 0 auto;\n  margin-top: 10px;\n}\n\n.favorite__label {\n  font-size: 50px;\n  font-family: 'Oswald', sans-serif;\n  text-align: left;\n}\n\n.lineFavorite {\n  background-color: gray;\n  height: 7px;\n  width: 100px;\n  margin-top: 10px;\n}\n\n/*\n * post\n */\n\n.posts {\n  margin: 32px auto auto;\n  text-align: left;\n}\n\n/*\n * post item\n */\n\n.post-item {\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n  width: 100%;\n  border-radius: 5px;\n  overflow: hidden;\n}\n\n.post-item__content {\n  padding: 16px;\n}\n\n.post-item__thumbnail {\n  width: 100%;\n  height: 70%;\n}\n\n.post-item__date {\n  font-size: 20px;\n  text-transform: uppercase;\n  color: #999;\n}\n\n.post-item__date__author {\n  color: red;\n  text-decoration: none;\n}\n\n.post-item__title {\n  font-weight: 500;\n  font-size: 44px;\n  transition: 0.3s opacity;\n}\n\n.post-item__title:hover {\n  color: gray;\n}\n\n.post-item__title a {\n  text-decoration: none;\n  color: inherit;\n}\n\n.post-item__description {\n  margin-top: 16px;\n  font-size: 25px;\n  line-height: 1.5em;\n  text-align: justify;\n}\n\n.nav {\n  width: 100%;\n  margin-left: auto;\n  margin-right: auto;\n  text-align: center;\n  display: none;\n}\n\n.nav__list {\n  width: 100%;\n  padding: 0;\n  margin: 0;\n}\n\n.nav__item {\n  box-sizing: border-box;\n  display: inline-block;\n  width: 24%;\n  font-size: 44px;\n  text-align: center;\n  line-height: 24px;\n  text-transform: uppercase;\n}\n\n.nav a {\n  display: inline-block;\n  padding: 1.3em;\n  text-decoration: none;\n  color: #616161;\n}\n\n.nav a:hover {\n  text-decoration: underline;\n  color: gray;\n}\n\n.skip-to-content {\n  position: absolute;\n  top: -40px;\n  left: 0;\n  background-color: #bf1722;\n  color: white;\n  padding: 8px;\n  z-index: 100;\n  text-decoration: none;\n  font-family: sans-serif;\n}\n\n.skip-to-content:focus {\n  top: 0;\n}\n\nfooter {\n  background-color: #363636;\n  text-align: center;\n  padding: 40px;\n  margin-top: 50px;\n}\n\nfooter p {\n  font-size: 30px;\n  color: #ffffff;\n  margin: 0;\n  font-family: 'Graduate', cursive;\n}\n\n/*Detail.css*/\n.container-detail {\n  padding: 20px;\n  font-family: 'Roboto', sans-serif;\n}\n\n.wrapper {\n  display: grid;\n  grid-template-columns: repeat(3, 1fr);\n  padding: 20px;\n  column-gap: 10px;\n  row-gap: 10px;\n  /* border: 1px solid black; */\n  background-color: rgba(0, 0, 0, 0);\n  border-radius: 20px;\n}\n\n.wrapper div {\n  /* border: 1px solid black; */\n  padding: 16px;\n}\n\n.wrapper td {\n  width: 100%;\n  border-bottom: 1px solid #000746;\n}\n\n.wrapper th {\n  text-align: left;\n  width: 20%;\n  border-bottom: 2px solid #000000;\n}\n\n.wrapper table {\n  width: 100%;\n\n}\n\n.wrapper tr {\n  height: 50px;\n  font-size: 18px;\n}\n\n.list-menu,\n.menu-item {\n  text-align: left;\n  list-style: none;\n  font-size: 20px;\n  line-height: 3;\n}\n\n.list-review,\n.item-list-review {\n  list-style: none;\n  font-size: 20px;\n  line-height: 2;\n}\n\n\n.box1 {\n  color: black;\n  grid-column-start: 1;\n  grid-column-end: 4;\n  text-align: left;\n  padding: 0;\n  width: 100%;\n  height: 70%;\n}\n\n.box2 {\n  grid-column-start: 1;\n  grid-column-end: 3;\n  padding: 0;\n  /* border: 1px solid white; */\n}\n\n.box3 {\n  grid-column-start: 3;\n  grid-column-end: 4;\n  padding: 0;\n  width: 100%;\n  /* margin: 0 auto; */\n  /* width: 80%; */\n  /* border: 2px solid yellow; */\n}\n\n.box4 {\n  padding: 0;\n  grid-column-start: 1;\n  grid-column-end: 3;\n  width: auto;\n  text-align: justify;\n}\n\n.box5 {\n  grid-column-start: 1;\n  grid-column-end: 2;\n  border-radius: 10px;\n}\n\n.box6 {\n  grid-column-start: 2;\n  grid-column-end: 3;\n  border-radius: 10px;\n}\n\n.box7 {\n  grid-column-start: 1;\n  grid-column-end: 3;\n}\n\n.info-box {\n  /* border: 1px solid rgb(111, 111, 111); */\n  background-color: rgba(255, 255, 255, 0);\n  border-radius: 10px;\n  margin: 0 auto;\n  height: 30%;\n}\n\n#desc-box {\n  /* border: 1px solid rgb(111, 111, 111); */\n  background-color: rgba(111, 111, 111, 0);\n  border-radius: 10px;\n  line-height: 30px;\n  padding: 0;\n}\n\n#foods-box {\n  /* border: 1px solid rgb(111, 111, 111); */\n  background-color: rgb(111, 111, 111, 0);\n  border-radius: 10px;\n  padding: 0;\n}\n\n#drinks-box {\n  /* border: 1px solid rgb(111, 111, 111); */\n  background-color: rgb(111, 111, 111, 0);\n  border-radius: 10px;\n  padding: 0;\n}\n\n#customerReview-box {\n  /* border: 1px solid rgb(111, 111, 111); */\n  background-color: rgb(111, 111, 111, 0);\n  border-radius: 10px;\n  padding: 0;\n}\n\n#review-container {\n  padding: 0;\n}\n\nimg#restaurant-img {\n  margin: 0 auto;\n  max-width: 100%;\n  height: auto;\n  object-fit: cover;\n  /* border: 1px solid rgb(111, 111, 111); */\n  border-radius: 10px;\n}\n\n.wrapper h4 {\n  text-align: left;\n  font-size: 30px;\n  color: rgb(0, 0, 0);\n  font-family: 'Oswald', sans-serif;\n}\n\n.box1 h1 {\n  font-size: 50px;\n  font-family: 'Oswald', sans-serif;\n}\n\n.custName {\n  font-weight: bold;\n  color: rgb(70, 70, 70);\n}\n\n.custDate {\n  color: rgb(138, 138, 138);\n}\n\n.reviewText {\n  border: 1px solid rgb(222, 221, 221);\n  padding: 5px;\n  margin-bottom: 15px;\n  border-radius: 7px;\n  color: rgb(104, 104, 104);\n  font-family: 'Roboto', sans-serif;\n}\n\n.wrapper hr {\n  background-color: black;\n  width: 100px;\n  margin-top: 10px;\n  margin-bottom: 10px;\n  height: 6px;\n  border: none;\n}\n\n.list-menu hr {\n  background-color: rgb(104, 104, 104);\n  width: 100%;\n  height: 1px;\n}\n\n/*\n  Like\n*/\n.like {\n  width: 55px;\n  height: 55px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background-color: gray;\n\n  position: fixed;\n  bottom: 16px;\n  right: 16px;\n  border-radius: 50%;\n  border: 0;\n\n  font-size: 18px;\n  color: white;\n  cursor: pointer;\n}\n\n#not-found{\n  font-family: 'Roboto', sans-serif;\n  color: gray;\n  font-size: 20px;\n  text-align: left;\n  text-decoration: underline;\n}"],"sourceRoot":""}]);
